
body {
  background-color: #1F2734;
}
button,
label,
.btn {
  text-transform: capitalize !important;
}
span {
  color: white;
  margin-right: 10px;
}
hr {
  border: 0;
  height: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
.delete {
  margin: 0px 15px 0px;
}
.title {
  color: white;
}
.add-button {
  margin-left: 14px;
  text-transform: capitalize;
  margin-bottom: 10px;
  width: 150px;
}
.main-color {
  color: #FCAF19;
}
.secondary-content {
  margin: 0px 10px;
  color: #2196F3;
}
.filter-list {
  margin: 20px 0px;
}
.first-filter {
  margin-bottom: 20px;
}
.filter-list button {
  width: 150px;
  margin: 0px 25px 0px 0px;
}
.second-filter button.blue {
  margin-left: 5px;
}
.people-list {
  color: white;
}
table thead {
  color: #FCAF19;
}
table thead tr th {
    font-weight: 400;
}
table tbody {
  color: white;
}
li.collection-item {
  margin: 15px 0px;
}
.form {
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 40px;
  margin-top: 40px;
  width: 600px;
}
.form h5 {
    margin-bottom: 40px;
}
.form .row input {
    background: #3E4A5E;
    border: 1px solid #3E4A5E !important;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    color: white;
    padding-left: 10px;
    font-size: 14px;
}
.form .row input:focus {
      -webkit-box-shadow: none !important;
              box-shadow: none !important;
}
.form .row .size input {
    width: 33.333%;
    float: left;
}
.form .row .size input:nth-child(1) {
      margin-right: 22px;
}
.form .row .buttons {
    text-align: right;
    margin-top: 20px;
}
.form .row .buttons a {
      margin-right: 25px;
}
.form .row label {
    width: 110px;
}
.form .row label:nth-child(2) {
      margin-right: 22px;
}
.form .row input[type="radio"]:checked + label {
    background-color: #2196F3 !important;
}
